*{
    margin:0;padding: 0;box-sizing: border-box;
}
.container{
    height:100vh;width: 100vw;display: flex;
    justify-content: center;
    align-items: center;
}

.box{
    height:100vh;width: 100vw;display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.search{
    height:5.5rem;width:22rem;display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.searchtop{
    width: 100%;
    height:auto;display: flex;
padding:0 15px 0 15px;
    align-items: center;position: relative;
    margin: 10px;
}
.mode{
height: 100%;display: flex;
justify-content: center;cursor:pointer;
align-items: center;position: absolute;right:15px;
}
.searchdown{
    height: 50%;width: 100%;
    position: relative;border-radius:6px;
    overflow: hidden;
    box-shadow:1px 2px 5px black;

}
.searchdown input{
    padding:0 0 0 6px;
    height: 100%;width: 100%;outline: none;border: 0px;
    
}
.searchdown button{
    height:2.1rem;width:80px;
    position: absolute;right:4px;top:5.4px;
    outline: none;border: 0;background-color: blue;color: white;
    font-weight: 550;border-radius: 6px;cursor: pointer;
    
}
/* data box */
.datbox{
border-radius:10px;
    height:240px;
    width: 550px;
margin-top:25px;

    display: flex;
    
}
.dataleft{
    height:100%;
    width:30%;
  
    display: flex;align-items: center;padding:0 0 0 15px;
}
.dataleft img{
    height:130px;width: 130px;
    border-radius: 50%;text-align: center;
    
}

/* right data box */
.dataright{
    height:100%;
    width:70%;

    display: flex;align-items: center;padding:10px 0 10px 15px;
    flex-direction: column;
}
.name{
    height:auto;
    width:100%;
    display: flex;justify-content: center;
    align-items: center;padding:4px 0 4px 0 ;

}
.name p{
    padding:0 4px 0 10px;
}
.college{
    height:auto;
    width:80%;font-weight: 550;
    display: flex;justify-content: center;text-align: center;
    align-items: center; padding:10px 10px 10px 10px;
}
.followerbox{
    height:50px;padding: 1px 2px 1px 2px;
    width:78%;margin-top:5px;
    display: flex;justify-content: center;border-radius: 10px;
    align-items: center;background-color: rgb(89, 174, 174);
}
.followerbox h3{
    font-size: 15px;
}
.followerbox p{
    font-weight: 550;
}
.info{
    width: 30%;height: 100%;
    display: flex;justify-content: center;
    flex-direction: column;
    margin:10px ;
    padding: 0;
}
.location{
    height:36px;margin-top:13px;font-weight:550;
    width:100%;
    display: flex;justify-content: center;
    align-items: center;
}
.sharepart{
    height:40px;
    width:100%;
    display: flex;justify-content: center;
    align-items: center;cursor: pointer;
}
.share{
    height:30px;
    width:auto;
    display: flex;
    align-items: center;margin:10px;font-weight: 550;
    font-size: 15px;cursor: pointer;
}
.market a{
    height:30px;
    width:auto;
    display: flex;
    align-items: center;font-weight: 550;
  cursor: pointer;text-decoration: none;
}
.market a i{
    padding:0 0px  0 7px ;
}

@media only screen and (max-width:600px){

.container{
    height: 100vh;width: 100vw;position: relative;
}

    .box{
        position: absolute;
        height:auto;width: 100%;display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;top: 10px;
    }
    .search{
        height:5.5rem;width:18rem;display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
    }
    .searchtop{
        width: 100%;
    
    }



    .datbox{
        border-radius:10px;
            height:500px;
            width:85%;
        margin-top:25px;
        
            display: flex;
            flex-direction: column;
            
        }
        .dataleft{
            height:30%;
            width:100%;
          justify-content: center;
            display: flex;align-items: center;
        }
        .dataleft img{
            height:120px;width: 120px;
          
            
        }
        .name{
            flex-direction: column;
        }
        /* right data box */
        .dataright{
            height:70%;
            width:100%;
        justify-content: center;
            display: flex;align-items: center;
            flex-direction: column;
        }

}